module.exports = "exports.render = function() {\r\n\treturn require(\"./content.jade\")();\r\n};\r\n\r\nexports.start = function() {\r\n\tvar THREE = require(\"three\");\r\n\trequire(\"jquery\");\r\n\r\n\t// three.js canvas - camera - orthographic example\r\n\r\n\tvar container, stats;\r\n\tvar camera, scene, renderer;\r\n\r\n\tinit();\r\n\tanimate();\r\n\r\n\tfunction init() {\r\n\r\n\t\tcamera = new THREE.OrthographicCamera( -400, 400, 400, -400, - 2000, 1000 );\r\n\t\tcamera.position.x = 200;\r\n\t\tcamera.position.y = 100;\r\n\t\tcamera.position.z = 200;\r\n\r\n\t\tscene = new THREE.Scene();\r\n\r\n\t\tscene.add( camera );\r\n\r\n\t\t// Grid\r\n\r\n\t\tvar geometry = new THREE.Geometry();\r\n\t\tgeometry.vertices.push( new THREE.Vertex( new THREE.Vector3( - 500, 0, 0 ) ) );\r\n\t\tgeometry.vertices.push( new THREE.Vertex( new THREE.Vector3( 500, 0, 0 ) ) );\r\n\r\n\t\tfor ( var i = 0; i <= 20; i ++ ) {\r\n\r\n\t\t\tvar line = new THREE.Line( geometry, new THREE.LineBasicMaterial( { color: 0x000000, opacity: 0.2 } ) );\r\n\t\t\tline.position.z = ( i * 50 ) - 500;\r\n\t\t\tscene.add( line );\r\n\r\n\t\t\tline = new THREE.Line( geometry, new THREE.LineBasicMaterial( { color: 0x000000, opacity: 0.2 } ) );\r\n\t\t\tline.position.x = ( i * 50 ) - 500;\r\n\t\t\tline.rotation.y = 90 * Math.PI / 180;\r\n\t\t\tscene.add( line );\r\n\r\n\t\t}\r\n\r\n\t\t// Cubes\r\n\r\n\t\tgeometry = new THREE.CubeGeometry( 50, 50, 50 );\r\n\t\tvar material = new THREE.MeshLambertMaterial( { color: 0xffffff, shading: THREE.FlatShading, overdraw: true } );\r\n\r\n\t\tfor ( i = 0; i < 100; i ++ ) {\r\n\r\n\t\t\tvar cube = new THREE.Mesh( geometry, material );\r\n\r\n\t\t\tcube.scale.y = Math.floor( Math.random() * 2 + 1 );\r\n\r\n\t\t\tcube.position.x = Math.floor( ( Math.random() * 1000 - 500 ) / 50 ) * 50 + 25;\r\n\t\t\tcube.position.y = ( cube.scale.y * 50 ) / 2;\r\n\t\t\tcube.position.z = Math.floor( ( Math.random() * 1000 - 500 ) / 50 ) * 50 + 25;\r\n\r\n\t\t\tscene.add(cube);\r\n\r\n\t\t}\r\n\r\n\t\t// Lights\r\n\r\n\t\tvar ambientLight = new THREE.AmbientLight( Math.random() * 0x10 );\r\n\t\tscene.add( ambientLight );\r\n\r\n\t\tvar directionalLight = new THREE.DirectionalLight( Math.random() * 0xffffff );\r\n\t\tdirectionalLight.position.x = Math.random() - 0.5;\r\n\t\tdirectionalLight.position.y = Math.random() - 0.5;\r\n\t\tdirectionalLight.position.z = Math.random() - 0.5;\r\n\t\tdirectionalLight.position.normalize();\r\n\t\tscene.add( directionalLight );\r\n\r\n\t\tdirectionalLight = new THREE.DirectionalLight( Math.random() * 0xffffff );\r\n\t\tdirectionalLight.position.x = Math.random() - 0.5;\r\n\t\tdirectionalLight.position.y = Math.random() - 0.5;\r\n\t\tdirectionalLight.position.z = Math.random() - 0.5;\r\n\t\tdirectionalLight.position.normalize();\r\n\t\tscene.add( directionalLight );\r\n\r\n\t\trenderer = new THREE.CanvasRenderer();\r\n\t\trenderer.setSize( 400, 400 );\r\n\r\n\t\t$(\".three-container\").append(renderer.domElement);\r\n\t}\r\n\r\n\tfunction animate() {\r\n\t\tif($(\".three-container\").length > 0) {\r\n\t\t\trequestAnimationFrame( animate );\r\n\t\t\trender();\r\n\t\t}\r\n\t}\r\n\tfunction render() {\r\n\t\tvar timer = new Date().getTime() * 0.0001;\r\n\r\n\t\tcamera.position.x = Math.cos( timer ) * 200;\r\n\t\tcamera.position.z = Math.sin( timer ) * 200;\r\n\t\tcamera.lookAt( scene.position );\r\n\r\n\t\trenderer.render( scene, camera );\r\n\t}\r\n};"